
* {
  /* font-family: 'Encode Sans Expanded', sans-serif; */
  font-family: 'Open Sans Condensed', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

p {
  color: rgba(0, 0, 0, 0.68);
  font-weight: 100;
  font-size: 1.1em;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.68);
  -webkit-transition: all 0.5s ease-in;
  -o-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
}

a:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
  color: #FF9500;
}

.limit-width {
  max-width: 1200px;
  margin: 0 15px;
}

.limit-width-1000px {
  max-width: 1000px;
  margin: 0 15px;
}

/* 
* HEADER 
*/
.Header {
  position: relative;
  width: 100%;
  height: 300px;
  max-height: 330px;
  margin-bottom: 50px;
  text-align: right;
}

.Header-responsive {
  width: 100%;
  height: 100%;
  /* background-image:  url(./al2.jpg); */
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.4)), to(rgba(0, 0, 0, 0.1)) ), url(./images/al.jpg);
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.1) ), url(./images/al.jpg);
  background-image: -o-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.1) ), url(./images/al.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.1) ), url(./images/al.jpg);
  background-position: center top;
  background-size: cover;
  -webkit-clip-path: polygon(0 0, 100% 0, 100% 30%, 100% 100%, 73% 89%, 47% 100%, 21% 89%, 0 100%);
          clip-path: polygon(0 0, 100% 0, 100% 30%, 100% 100%, 73% 89%, 47% 100%, 21% 89%, 0 100%);
}

.my-info {
  position: relative;
  text-align: left;
}

.profile-picture {
  position: absolute;
  width: 120px;
  height: 120px;
  top: 5px;
  right: -10px;
  background-image:  url(./images/headshot.jpg);
  background-image: center center;
  background-size: cover;
  -webkit-clip-path: circle(50% at 50% 50%);
          clip-path: circle(50% at 50% 50%);
}

.Header h1, h4, h5 {
  color: whitesmoke;
  margin: 0 auto;
}

.Header h5 {
  position: absolute;
  left: 2%;
  bottom: 30px;
  font-size: 0.8em;
  font-variant: small-caps;
}

/*
 * MAINPAGE 
 */
.MainPage {
  /* max-width: 1200px; */
  height: 100%;
  margin-bottom: 150px;
  /* margin: 15px 15px; */
  color: #61A6E3;
  /* background-color: #675C5C; */
}

.MainPage * > h2 {
  margin-bottom: 8px;
}

.MainPage * > article {
  /* width: 100%; */
  margin: 10px 0;
  text-align: center;
}

/* ABOUT ME & INTERESTS */
.MainPage .intro-info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

/* MARSTON IMAGE */
.MainPage .responsive-container {
  width: 100%;
  height: 300px;
  max-height: 330px;
  margin: 60px 0;
}

.MainPage .full-width-image {
  width: 100%;
  height: 100%;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.4)), to(rgba(0, 0, 0, 0.1)) ), url(./images/msl2.jpg);
  background-image: -webkit-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.1) ), url(./images/msl2.jpg);
  background-image: -o-linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.1) ), url(./images/msl2.jpg);
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.1) ), url(./images/msl2.jpg);
  background-position: center center;
  background-size: cover;
  -webkit-clip-path: polygon(74% 10%, 100% 0, 100% 100%, 76% 85%, 49% 100%, 20% 88%, 0 100%, 0 0, 18% 11%, 49% 0);
          clip-path: polygon(74% 10%, 100% 0, 100% 100%, 76% 85%, 49% 100%, 20% 88%, 0 100%, 0 0, 18% 11%, 49% 0);
}

/* 
* TECHNICAL DETAILS
*/

.Technical-Details {
  height: 150px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.Technical-Details > h1 {
  margin-right: 30px;
  margin-bottom: 10px;
  font-size: 2.7em;
}

.Technical-Details .technical-content {
  -ms-flex-positive: 1;
      flex-grow: 1; /* So that the list will take the remaining width */
}

.Technical-Details .technical-content > div {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  margin-bottom: 15px;
  padding-left: 10px;
  border-left: 1px solid rgba(0, 0, 0, 0.51);
}

.Technical-Details .flex-container {
  height: 50%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}

.Technical-Details .flex-container > h2 {
  font-size: 1.7em;
}

.Technical-Details .language_list, .project-list {
  text-align: left;
  width: 100%;  /* To take up the full screen width on small devices */
}

.Technical-Details .language_list > li, .project-list > li {
  display: inline;
  margin: 0 15px 0 0;
  /* margin: 0 15px 5px auto; */
  color: rgba(0, 0, 0, 0.68);
  font-size: .8em;
  -webkit-transition: all 0.5s ease;
  -o-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.Technical-Details .language_list > li:hover {
  /* transform: scale(3); */
  color: #4F9CE0;
}

.MainPage .language_list > li:hover {
  /* transform: scale(3); */
  color: #4F9CE0;
}

/* When the screen is larger than an iphone7+ */
@media screen and (min-width: 600px) {
  .MainPage {
    margin-bottom: 0;
  }
  .MainPage * > article {
    min-width: 47%; /* Set max-width of articles to avoid using margin/padding */
    max-width: 47%;
    text-align: center;
  }
  .MainPage .intro-info {
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
  }
  .Technical-Details {
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
  }
  .Technical-Details .language_list, .project-list {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }
  .Technical-Details h1 {
    font-size: 3em;
    margin-bottom: 0;
  }
  .Technical-Details .flex-container > h2 {
    font-size: 2em;
  }
  .Technical-Details .language_list > li, .project-list > li {
    font-size: 1em;
  }
}

@media screen and (max-width: 890px) {
  .Technical-Details .language_list, .project-list {
    width: 100%;
  }
}

/* When the screen size is bigger than 1200px (MAX Width) */
@media screen and (min-width: 1201px) {
  .MainPage {
    margin: 15px auto;
  }
  .limit-width {
    margin: 0 auto;
  }
  .limit-width-1000px {
    margin: 0 auto;
  }
}


/*
* FAB 
*/
.FAB {
  position: fixed;
  padding: 0 10px 15px 10px;
  right: 5%;
  bottom: 14%;
  width: 50px;
  height: 50px;
  text-align: center;
  /* color: whitesmoke; */
  /* background-color: #f37021; */
  background-color: #f47a31;
  -webkit-clip-path: polygon(0% 0%, 100% 0%, 100% 75%, 75% 75%, 75% 100%, 50% 75%, 0% 75%);
          clip-path: polygon(0% 0%, 100% 0%, 100% 75%, 75% 75%, 75% 100%, 50% 75%, 0% 75%);
  -webkit-transition: all 0.5s ease-in;
  -o-transition: all 0.5s ease-in;
  transition: all 0.5s ease-in;
}

.FAB:hover {
  -webkit-transform: scale(1.2);
      -ms-transform: scale(1.2);
          transform: scale(1.2);
}

/*
* FOOTER 
*/
.Footer {
  width: 100%;
  margin-top: 50px;
  padding: 10px 0 0 0;
  text-align: center;
  background-color: #001c35;;
}

.Footer p {
  color: whitesmoke;
}

.Footer-content {
  /* background-color: #001c35; */
}
.Footer-copyright {
  margin-top: 10px;
  padding: 10px 0;
  background-color: #001324;
}


